import React from 'react';
import './status.css';

export default (props) => {
    const status = (props.gameOver && <p>Result:<br /> <span className="gameOver">{props.gameOver}</span></p>) 
                        || <p>Next move:<br /> {props.turn}</p>;
    return (
        <div className="gameStatus">{status}</div>
    )
}